 *, *:before, *:after {
	 box-sizing: border-box;
}

 .set-size {
     width: 40%;
	 font-size: 10em;
}
 .charts-container:after {
	 clear: both;
	 content: '';
	 display: table;
}
 .pie-wrapper {
	 height: 1em;
	 width: 1em;
	 float: left;
	 margin: 15px;
	 position: relative;
}
 .pie-wrapper:nth-child(3n + 1) {
	 clear: both;
}
 .pie-wrapper .pie {
	 height: 100%;
	 width: 100%;
	 clip: rect(0, 1em, 1em, 0.5em);
	 left: 0;
	 position: absolute;
	 top: 0;
}
 .pie-wrapper .pie .half-circle {
	 height: 100%;
	 width: 100%;
	 border: 0.1em solid #3498db;
	 border-radius: 50%;
	 clip: rect(0, 0.5em, 1em, 0);
	 left: 0;
	 position: absolute;
	 top: 0;
}
 .pie-wrapper .label {
	 background: #34495e;
	 border-radius: 50%;
	 bottom: 0.4em;
	 color: #ecf0f1;
	 cursor: default;
	 display: block;
	 font-size: 0.25em;
	 left: 0.4em;
	 line-height: 2.8em;
	 position: absolute;
	 right: 0.4em;
	 text-align: center;
	 top: 0.4em;
}
 .pie-wrapper .label .smaller {
	 color: #bdc3c7;
	 font-size: 0.45em;
	 padding-top: 20px;
	 vertical-align: super;
}
 .pie-wrapper .shadow {
	 height: 100%;
	 width: 100%;
	 border: 0.1em solid #bdc3c7;
	 border-radius: 50%;
}
 .pie-wrapper.style-2 .label {
	 background: none;
	 color: #7f8c8d;
}
 .pie-wrapper.style-2 .label .smaller {
	 color: #bdc3c7;
}
 .pie-wrapper.progress-30 .pie .half-circle {
	 border-color: #3498db;
}
 .pie-wrapper.progress-30 .pie .left-side {
	 -webkit-transform: rotate(108deg);
	         transform: rotate(108deg);
}
 .pie-wrapper.progress-30 .pie .right-side {
	 display: none;
}
